.information {
  padding: 20px;
}
.information .ant-descriptions-title {
  display: flex;
  justify-content: center;
  align-items: center;
}
.information .ant-descriptions-title .ant-image {
  margin-right: 10px;
}
.information .ant-descriptions-title .memberList {
  height: 210px;
  display: flex;
  flex-wrap: wrap;
  overflow: auto;
}
.information .ant-descriptions-title .memberList .listItem {
  margin-bottom: 10px;
}
.information .ant-descriptions-title .memberList .listItem .ant-image {
  margin: 0 10px;
}
.information .ant-descriptions-title .memberList .listItem .name {
  font-size: 12px;
  font-weight: normal;
  text-align: center;
  width: 55px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
