.main {
  width: 100%;
  height: 100%;
  background: #f0f2f5;
  overflow: auto;
}
.main-content {
  width: 1200px;
  margin: 0 auto;
  height: calc(100% - 60px);
  padding: 30px 0;
  min-height: 610px;
}
.main-content .ant-collapse {
  background-color: #fff;
}
.main-content .ant-collapse .ant-collapse-item {
  border-bottom: 0;
}
.main-content .ant-collapse .ant-collapse-item .ant-collapse-header .ant-collapse-arrow {
  margin-top: -4.5px;
}
.main-content .side-menu {
  width: 50px;
  height: 100%;
  float: left;
  background: #353434;
  text-align: center;
}
.main-content .side-menu .ant-avatar {
  margin: 20px 0;
  cursor: pointer;
}
.main-content .side-menu .ant-menu {
  height: calc(100% - 80px);
  background: transparent;
  border-right: 0;
}
.main-content .side-menu .ant-menu .ant-menu-item {
  background: transparent;
  margin: 0;
  height: 50px;
  line-height: 50px;
  padding: 0;
  text-align: center;
}
.main-content .side-menu .ant-menu .ant-menu-item a {
  color: #A68438;
}
.main-content .side-menu .ant-menu .ant-menu-item a:hover {
  color: #FDBE32;
}
.main-content .side-menu .ant-menu .ant-menu-item-selected a {
  color: #FDBE32;
}
.main-content .main-page {
  width: calc(100% - 50px);
  height: 100%;
  float: left;
  display: flex;
  background: #fff;
}
.main-content .main-page-top {
  height: 60px;
}
.main-content .main-page-top.main-page-search {
  padding: 14px 15px;
  border-bottom: 2px solid rgba(0, 0, 0, 0.06);
}
.main-content .main-page-top.main-page-search .ant-input-affix-wrapper {
  background: #f1f2f7;
  border: 0;
}
.main-content .main-page-top.main-page-search .ant-input-affix-wrapper .ant-input {
  background: transparent;
  font-size: 12px;
}
.main-content .main-page-top-title {
  text-align: center;
  line-height: 58px;
  border-bottom: 2px solid rgba(0, 0, 0, 0.06);
  box-sizing: border-box;
}
.main-content .main-page-content {
  height: calc(100% - 60px);
}
.main-content .main-page-list {
  width: 230px;
  border-right: 2px solid rgba(0, 0, 0, 0.06);
}
.main-content .main-page-chat {
  width: 620px;
  border-right: 2px solid rgba(0, 0, 0, 0.06);
}
.main-content .main-page-chat .main-page-top {
  background: #f5f5f5;
  border-bottom: 2px solid rgba(0, 0, 0, 0.06);
  display: flex;
  align-items: center;
  padding-left: 20px;
}
.main-content .main-page-info {
  width: 300px;
}
.main-content .main-page-info .ant-tabs-bar {
  margin: 0;
}
.main-content .main-page-info .ant-tabs-nav {
  height: 60px;
  width: 100%;
  border-bottom: 2px solid rgba(0, 0, 0, 0.06);
}
.main-content .main-page-info .ant-tabs-nav .ant-tabs-tab {
  width: 50%;
  margin: 0;
  text-align: center;
  line-height: 60px;
  padding: 0;
  font-size: 16px;
  color: #959cb6;
}
.main-content .main-page-info .ant-tabs-nav .ant-tabs-tab:hover {
  color: #FDBE32;
}
.main-content .main-page-info .ant-tabs-nav .ant-tabs-tab-active {
  color: #FDBE32;
}
.main-content .main-page-info .ant-tabs-nav .ant-tabs-tab img {
  margin-right: 8px;
}
.main-content .main-page-info .ant-tabs-nav .ant-tabs-ink-bar {
  display: none !important;
}
::-webkit-scrollbar {
  /*滚动条整体样式*/
  width: 6px;
  /*高宽分别对应横竖滚动条的尺寸*/
  height: 1px;
}
::-webkit-scrollbar-thumb {
  /*滚动条里面小方块*/
  border-radius: 5px;
  -webkit-box-shadow: inset 0 0 5px rgba(0, 0, 0, 0.2);
  background: #C3C6D6;
}
::-webkit-scrollbar-track {
  /*滚动条里面轨道*/
  -webkit-box-shadow: inset 0 0 5px #F7F8FA;
  border-radius: 5px;
  background: #F9F9F9;
}
