.profile {
  padding: 20px;
}
.profile-title {
  display: flex;
  border-bottom: 1px solid #f2f2f2;
  padding-bottom: 20px;
}
.profile-title .ant-image {
  margin-left: 20px;
}
.profile-name {
  color: #000;
  margin-bottom: 5px;
}
.profile-account {
  color: #999;
  margin-bottom: 0;
}
.profile-extra {
  padding-top: 20px;
}
.profile-area {
  margin-bottom: 4px;
}
.profile-area label {
  color: #999;
}
.profile-icons {
  margin-top: 20px;
  text-align: right;
}
.profile-icons .anticon {
  cursor: pointer;
}
