.contact-list {
  height: 100%;
  overflow: auto;
}
.contact-list .ant-collapse {
  border: 0;
}
.contact-list .ant-collapse .ant-collapse-content > .ant-collapse-content-box {
  padding: 16px 0;
}
.contact-list .ant-list-item {
  position: relative;
  display: block;
  padding: 10px 15px;
  overflow: hidden;
  border-bottom: 1px solid #f0f2f5;
  cursor: pointer;
}
.contact-list .ant-list-item .ant-list-item-meta-avatar {
  margin-right: 10px;
}
.contact-list .ant-list-item .ant-list-item-meta-content .ant-list-item-meta-title {
  font-size: 13px;
  margin-bottom: 0;
  width: 85px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.contact-list .ant-list-item .ant-list-item-meta-content .ant-list-item-meta-description {
  font-size: 12px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.contact-list .ant-list-item .showTime {
  position: absolute;
  font-size: 12px;
  color: #999999;
  top: 10px;
  right: 15px;
}
.contact-list .ant-list-item.active {
  background: #f5f5f5;
}
.contact-list .ant-list-item:hover {
  background: #f5f5f5;
}
