.message {
  background: #F5F5F5;
  height: 100%;
  width: 100%;
}
.message-list {
  height: 70%;
  width: 100%;
  overflow-y: auto;
  padding: 12px 20px;
}
.message-list-own {
  text-align: right;
  margin-bottom: 10px;
}
.message-list-own .message-list-content {
  background: #9EEA6A;
}
.message-list-own .message-list-content:before {
  content: "";
  position: absolute;
  right: -16px;
  top: 6px;
  width: 0;
  height: 0;
  border-top: 8px solid transparent;
  border-left: 8px solid #9EEA6A;
  border-right: 8px solid transparent;
  border-bottom: 8px solid transparent;
}
.message-list-other {
  text-align: left;
  margin-bottom: 10px;
}
.message-list-other .message-list-content {
  background: #ffffff;
}
.message-list-other .message-list-content:before {
  content: "";
  position: absolute;
  left: -16px;
  top: 6px;
  width: 0;
  height: 0;
  border-top: 8px solid transparent;
  border-left: 8px solid transparent;
  border-right: 8px solid #fff;
  border-bottom: 8px solid transparent;
}
.message-list-content {
  display: inline-block;
  max-width: calc(100% - 80px);
  margin: 0 20px;
  padding: 10px;
  box-sizing: border-box;
  border-radius: 4px;
  position: relative;
}
.message-send {
  height: 30%;
  width: 100%;
  border-top: 2px solid #f0f2f5;
}
.message-send-icon {
  height: 40px;
  padding-left: 20px;
  line-height: 50px;
  position: relative;
}
.message-send-icon .anticon {
  margin-right: 12px;
  cursor: pointer;
}
.message-send-box {
  height: calc(100% - 40px);
  padding-bottom: 20px;
}
.message-send-box-input {
  border: none;
  outline: none;
  width: 100%;
  height: 100%;
  background: #F5F5F5;
  padding: 0 30px;
  resize: none;
  margin: 0;
  font-size: 14px;
  white-space: pre-wrap;
  white-space: -moz-pre-wrap;
  white-space: -o-pre-wrap;
  word-wrap: break-word;
  overflow-y: auto;
}
